import {
  NUMBER_DON,
  NUMBER_POINT,
  EN_NUM_BRACKET,
  ZH_NUM_BRACKET,
  EN_NUM_HALF_BRACKET,
  ZH_NUM_HALF_BRACKET,
  EN_LET_HALF_BRACKET,
  ZH_LET_HALF_BRACKET,
} from '../regexp';

export default (chars) => {
  let typeChar = chars;
  if (
    NUMBER_DON.test(chars) ||
    NUMBER_POINT.test(chars) ||
    EN_NUM_BRACKET.test(chars) ||
    ZH_NUM_BRACKET.test(chars) ||
    EN_NUM_HALF_BRACKET.test(chars) ||
    ZH_NUM_HALF_BRACKET.test(chars) ||
    EN_LET_HALF_BRACKET.test(chars) ||
    ZH_LET_HALF_BRACKET.test(chars)
  ) {
    typeChar = '1.';
  }
  switch (typeChar) {
    case '*':
    case '+':
    case '-':
      return 'ulist';
    case '1.':
      return 'olist';
    case '[]':
    case '[x]':
      return 'tlist';
    default:
      return null;
  }
};
